.covers {
  width: 100%;
  height: 100%;
  height: 65vw;
  max-height: 650px;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
      
  @media (min-width: 768px) {
    height: 60vw;
  } 
      
  @media (min-width: 1200px) {
    height: 45vw;
  } 
}

.cover {
  width: 30%;
  height: 30vw;
  border-radius: 10px;
  box-shadow: 2px 2px 6px $bg-black;
  max-height: 300px;
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  position: relative;
      
  @media (min-width: 768px) {
    width: 28%;
    height: 27vw;
  } 
      
  @media (min-width: 1200px) {
    width: 26%;
    height: 20vw;
  } 

  &:hover {
    transform: scale(1.03);
  }

  img {
    width: 100%;
    height: 100%;
    max-height: 300px;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
    position: absolute;
  }
}